.transactionHistory {
    margin-top:  5px;
    margin-left: auto;
    margin-right: auto;
    padding: 15px;
    width: 250px;
    background-color: #FFD100;
    table-layout: fixed;
    border-collapse: collapse;
}

tbody tr:nth-child(odd) {
    background-color: #0082D1;
}
  
tbody tr:nth-child(even) {
    background-color: #FFD100;
}
  